<template>
    <div id="modals-box"></div>
    <ModalConfirm v-if="confirmIsShowed" />

    <template v-for="item in openedModals" :key="item.name">
        <template v-if="modalsComponents[item.name]">
            <component :is="modalsComponents[item.name]" :name="item.name" />
        </template>
    </template>
</template>

<script setup>
import { computed, onBeforeUnmount, defineAsyncComponent } from 'vue';

import ModalConfirm from '/components/modals/ModalConfirm.vue';
import ModalLoader from '/components/modals/ModalLoader.vue';
import ModalSuccess from '/components/modals/ModalSuccess.vue';

import { storeToRefs } from 'pinia';
import stores from '@/stores';
const modals = stores.modals();

const { openedModals, confirmSettings, modalsComponents } = storeToRefs(modals);

modals.register({
    loader: ModalLoader,
    welcome: defineAsyncComponent(() => import('@/components/modals/ModalWelcome.vue')),
    invite: defineAsyncComponent(() => import('@/components/modals/ModalInvite.vue')),
    success: ModalSuccess
});

const confirmIsShowed = computed(() => !!confirmSettings.value);

onBeforeUnmount(() => {
    modals.unRegister(['loader', 'welcome', 'success', 'invite']);
});
</script>
