export const maskTime = {
    mask: 'HH:MM',
    blocks: {
        HH: {
            mask: IMask.MaskedRange,
            from: 0,
            to: 23,
            maxLength: 2
        },
        MM: {
            mask: IMask.MaskedRange,
            from: 0,
            to: 59,
            maxLength: 2
        }
    }
};

export const maskPreviewTime = {
    mask: 'HH:MM',
    blocks: {
        HH: {
            mask: IMask.MaskedRange,
            from: 0,
            to: 59,
            maxLength: 2
        },
        MM: {
            mask: IMask.MaskedRange,
            from: 0,
            to: 59,
            maxLength: 2
        }
    },
    parse: (str) => {
        const [min, sek] = str.split(':').map(s => s.trim());

        return {
            min: Math.max(Math.min(parseInt(min) || 0, 59), 0),
            sek: Math.max(Math.min(parseInt(sek) || 0, 59), 0)
        }
    },
    format: ({ min, sek }) => {
        return `${ min && min > 9 ? '' : '0' }${ min || 0 }:${ sek && sek > 9 ? '' : '0' }${ sek || 0 }`
    }
};

