export async function fetchData({ url, method, data, params, auth = true, formData, contentType, signal, responseType, range, keepalive }) {
    if (!url) {
        return;
    }

    const headers = {};

    if (stores.locale().locale) {
        headers['Lang'] = stores.locale().locale;
    }

    if (range) {
        headers['Range'] = range;
    }

    if (auth) {
        headers['Authorization'] = `Bearer ${ stores.user().token }`;
    }

    if (contentType) {
        headers['Content-Type'] = contentType;
    }

    return await axios({
        baseURL: import.meta.env.VITE_APP_API_URL,
        method,
        url,
        data: formData || data,
        headers,
        signal,
        params,
        responseType,
        // paramsSerializer,
        withCredentials: false,
        keepalive,
        proxy: false 
    });
}

export function numberWithSpaces(x) {
    const parts = x.toString().split('.');
    parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    return parts.join('.');
}

export const code = () => {
    return (Math.random() + 1).toString(36).substring(7);
};

export const isMobile = () => {
    if (navigator.userAgentData) {
        const uaData = navigator.userAgentData;
        if (uaData.mobile !== undefined) {
            return uaData.mobile;
        }
    }

    // fallback   
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i
        .test(navigator.userAgent);
};


export const timePeriodPreview = (values, timeEnabled) => {
    if (values?.length !== 2) {
        if (values) {
            return moment(values).format(`${ timeEnabled ? 'HH:mm ' : '' }DD.MM.YYYY`);
        }
        return '';
    }

    const from = moment(values[0]);
    const to = moment(values[1]);

    if (from.isSame(to, 'date')) {
        if (timeEnabled) {
            return `${ from.format('HH:mm') }  ${ to.format('HH:mm') }, ${ from.format('DD MMMM YYYY') }`;
        } else {
            return from.format('DD MMMM YYYY');
        }
    }

    return `${ from.format(`${ timeEnabled ? 'HH:mm ' : '' }DD.MM.YYYY`) }  ${ to.format(`${ timeEnabled ? 'HH:mm ' : '' }DD.MM.YYYY`) }`;
};


