export const knownSites = new Map(
    Object.entries(knownSitesList).map(([domain, code]) => [code, domain])
);

export const getDomainCode = (url) => {
    try {
        const domain = new URL(url).hostname.replace(/^www\./, '');
        const parts = domain.split('.');
        return parts[0] || code();
    } catch {
        return code();
    }
}

export const detectSiteCode = (url) => {
    try {
        const parsed = new URL(url);
        const host = parsed.hostname.replace(/^www\./, '');

        for (const domain in knownSitesList) {
            if (host.includes(domain)) {
                return knownSitesList[domain];
            }
        }

        return getDomainCode(url);
    } catch {
        return code();
    }
}

export const isInvalidEmail = (email) => {
    if (!email) {
        return false;
    }

    return !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)
}

export const shuffleArray = (array) => {
    const arr = array.slice();
    for (let i = arr.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [arr[i], arr[j]] = [arr[j], arr[i]];
    }
    return arr;
}

export const createEmitter = () => {
    const events = new Map();

    return {
        on(event, handler) {
            if (!events.has(event)) {
                events.set(event, new Set());
            }
            events.get(event).add(handler);
            return () => this.off(event, handler);
        },
        off(event, handler) {
            if (!events.has(event)) return;
            events.get(event).delete(handler);
        },
        emit(event, ...args) {
            if (!events.has(event)) return;
            for (const h of Array.from(events.get(event))) {
                try {
                    h(...args);
                } catch (e) {
                    console.error('Emitter handler error', e);
                }
            }
        }
    };
};


let wakeLock;

export const keepAwake = async () => {
    if (wakeLock !== null && document.visibilityState === 'visible') {
        try {
            wakeLock = await navigator.wakeLock.request('screen');
            // console.log('Wake Lock ');
        } catch (err) {
            // console.error('   Wake Lock:', err);
        }
    }
}
