<template>
    <Teleport to="body">
        <span class="modal-box-background"
            :class="{ mobile: data.isMobile }"
            @touchmove.prevent=""
            @click="close"></span>

        <div :class="[
                'modal-box',
                `modal-box-${data.id}`,
                (props.full ? 'full' : ''),
                (props.size ? props.size : ''),
                (data.isMobile ? 'mobile' : '')
            ]"
            ref="optionsList"
            :style="{
                width: props.width ? props.width + 'px' : '',
                transform: data.transform,
                opacity: data.opacity,
                ...(data.bottom !== null ? { bottom: data.bottom } : {})
            }"
            @touchstart="startDrag"
            @touchmove.stop="onDrag"
            @touchend="endDrag"

            v-touch:swipe.top="onSwipeTop"
            v-touch:swipe.bottom="onSwipeBottom"
            @click="click"
        >
            <slot></slot>
        </div>
    </Teleport>
</template>

<script setup>
import { onMounted, onBeforeUnmount, ref, reactive } from 'vue';
import { useI18n } from 'vue-i18n';
import { placeOptionList, isMobile } from '/utils/utils.js';
import { message } from '/utils/utils';
import { code } from '/utils/utils';

const optionsList = ref(null);

const { t } = useI18n();

const props = defineProps({
    field: { type: [Object, null], default: null },
    full: { type: Boolean, default: false },
    size: { type: [String, null], default: null },
    width: { type: [Number, null], default: null },
    placeOptions: { type: Object, default: () => ({}) }
});

const data = reactive({
    id: code(),
    isAtTop: true,
    isAtBottom: false,
    isTouching: false,
    isScrolling: false,
    isMobile: isMobile(),

    transform: 'none',
    opacity: 1,
    startY: 0,
    bottom: null
});

const emit = defineEmits(['close', 'onClick', 'onSwipeTop', 'onSwipeBottom']);

const close = (e) => {
    emit('close', e);
};

const click = (e) => {
    emit('onClick', e);
};

const onSwipeTop = () => {
    if (data.isScrolling) {
        return;
    }

    emit('onSwipeTop');
};

const onSwipeBottom = () => {
    if (data.isScrolling) {
        return;
    }

    emit('onSwipeBottom');
};

const handleTouchStart = () => {
    data.isScrolling = false;
};

const handleScroll = () => {
    const ul = optionsList.value.querySelector('ul');

    if (ul) {
        data.isAtTop = ul.scrollTop === 0;
        data.isAtBottom = ul.scrollHeight - ul.scrollTop === ul.clientHeight;

        if (!data.isAtTop || !data.isAtBottom) {
            data.isScrolling = true;
        }
    }
};

const startDrag = (e) => {
    if (!data.isMobile) {
        return;
    }

    const touch = e.touches[0];
    data.startY = touch.clientY;
    data.isTouching = true;
};

const onDrag = (e) => {
    if (!data.isTouching || !data.isMobile) {
        return;
    }

    const touch = e.touches[0];

    if (data.isScrolling) {
        data.startY = touch.clientY;
    }

    const diff = touch.clientY - data.startY;

    if (diff <= 20) {
        return;
    }


    data.transform = `translateY(${ diff - 20 }px) !important`;
    data.opacity = `${1 - (diff - 20) / 500} !important`;
};

const endDrag = () => {
    data.transform = 'none';
    data.opacity = 1;
};

const onResize = () => {
    if (/iPhone|iPad|iPod/.test(window.navigator.userAgent)) {
        data.bottom = `${ window.innerHeight - window.visualViewport.height - window.visualViewport.offsetTop + 10 }px`;
    }
}

window.visualViewport?.addEventListener('resize', onResize);

onMounted(() => {
    if (!data.isMobile) {
        if (props.field) {
            placeOptionList({
                container: props.field,
                options: `.modal-box-${ data.id }`,
                ...props.placeOptions
            });
        }

        return;
    }

    const ul = optionsList.value?.querySelector('ul');

    if (ul) {
        ul.addEventListener('scroll', handleScroll);
        ul.addEventListener('touchstart', handleTouchStart);
    }

    if (data.isMobile) {
        document.querySelector('html').style['overflow-y'] = 'hidden';
    }
});

onBeforeUnmount(() => {
    if (data.isMobile) {
        document.querySelector('html').style['overflow-y'] = 'auto';
        window.visualViewport?.removeEventListener('resize', onResize);
    }
});
</script>

<style lang="scss">
.modal-box {
    display: flex;
    flex-direction: column;
    max-height: 40vh;
    z-index: 400;
    @include sub-bg;
    position: fixed;
    border-radius: 10px;
    padding: 8px 0;
    transition: transform 0.3s ease-out;
    min-width: 192px;

    &.size-l {
        max-height: 80vh;
    }

    & > ul {
        list-style: none;
        padding: 0;
        cursor: pointer;
        overflow-y: auto;
        overflow-x: hidden;
        border-radius: 8px;
        margin: 2px 0 0 0;
        display: flex;
        flex-direction: column;
        gap: 0;
        flex: 1 1 100%;
        height: 1%;
        @include scrollbar(var(--white-10), var(--dark-20), 6px);

        & > li {
            display: flex;
            flex-direction: row;
            padding: 10px 20px;
            align-items: center;
            justify-content: space-between;
            font: var(--font-body-2);
            color: var(--dark-500);
            cursor: pointer;
            gap: 20px;
            min-width: 192px;
            flex-shrink: 0;

            .skeleton-wrapper {
                width: 30px;
                height: 30px;
                margin: -7px;
                border-radius: 8px;
                overflow: hidden;
            }

            svg {
                fill: var(--dark-20);
                stroke: var(--dark-80);
                flex-shrink: 0;
                width: 16px;
                height: 16px;
                flex-shrink: 0;
            }

            span {
                margin-right: auto;
            }
            &.box-input {
                position: sticky;
                top: 0;
                width: 100%;
                // background: var(--white-80);
                z-index: 1;
                .field {
                    width: 100%;
                }
            }

            &.selected {
                color: var(--primary-500);
                svg {
                    &.check {
                        fill: var(--primary-500);
                        stroke: var(--tertiary-500);
                    }
                }
            }

            a {
                font: var(--font-body-2);
                color: var(--dark-500);
            }

            &:hover {
                color: var(--primary-500);

                & > svg {
                    fill: var(--primary-500);
                    stroke: var(--tertiary-500);
                }

                & > span {
                    color: var(--primary-500);
                }
            }

            &.disabled {
                color: var(--dark-20);
                cursor: default;
                & > svg {
                    fill: var(--dark-20);
                    stroke: var(--dark-20);
                }
                & > span {
                    color: var(--dark-20);
                }
            }

            &.range-box {
                display: flex;
                flex-direction: row;
                gap: 12px;
                align-items: center;
                flex-shrink: 0;
                padding-top: 2px;
                padding-bottom: 2px;
                cursor: default;
                min-width: 260px;

                & > label {
                    width: 48px;
                    height: 28px;
                    border-radius: 10px;
                    font: var(--font-caption);
                    background: var(--light-20);
                    color: var(--dark-500);
                    display: flex;
                    align-items: center;
                    justify-content: start;
                    padding: 0 8px;
                    user-select: none;
                    flex-shrink: 0;
                }
                & > div {
                    flex: 1 1 100%;
                    width: 1%;
                }
            }

            &.submenu {
                cursor: default;
                &:hover {
                    background: transparent;
                }
            }
        }
    }

    & > .loader {
        background-color: var(--white-500);
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 1;
        border-radius: 10px;
        & > div {
            margin: auto;
            width: 120px;
            height: 50px;
            position: relative;
            overflow: hidden;
        }
    }

    & > .header {
        border-top-right-radius: 20px;
        border-top-left-radius: 20px;
        min-height: 48px;
        display: flex;
        align-items: center;
        width: 100%;
        z-index: 1;
        padding: 0;

        h3 {
            margin: 0 auto;
            padding: 0 40px;
            font: var(--font-h5);
            font-weight: bold;
            color: var(--dark-500);
            text-align: center;
            display: flex;
            flex-direction: column;
            gap: 16px;
        }

        button {
            position: absolute;
            right: 10px;
            top: 10px;
            // top: 50%;
            // transform: translateY(-50%);
        }
    }

    & > .content {
        padding: 0 20px 20px;
        overflow: auto;
        flex: 1 1;
        display: flex;
        flex-direction: column;
        gap: 20px;
        z-index: 1;
        @include scrollbar();
    }

    &.mobile {
        @include mobile-options-list;

        & > ul {
            & > li {
                &.box-input {
                    position: sticky;
                }
            }
        }
    }

    &.full {
        max-height: 100%;
        height: 250px;
        width: 100%;
        border-radius: 0;
        padding-top: 30px;
        animation: fade-height .2s forwards;

        @keyframes fade-height {
            0% {
                height: 250px;
                transform: translateY(50px);
            }
            100% {
                height: 2500px;
                transform: translateY(0);
            }
        }
    }
}
.direction-rtl .modal-box {
    & > ul {
        & > li {
            span {
                margin-right: 0;
                margin-left: auto;
            }
        }
    }
}
</style>
