import { createRouter, createWebHistory } from 'vue-router';
import { ref } from 'vue';
import { message } from '/utils/utils';

import stores from '@/stores';

import { requests } from '/utils/requests';

const componentKey = ref('initial');

const handleRouterError = (error) => {
    const t = stores.locale().t();
    const errors = [
        {
            code: ['fetch', 'load', 'dynamically', 'importing'],
            action: () => message.error(t('common.errors.importError'))
        }
    ];

    const errorWords = error.message.toLowerCase().split(' ');
    const action = errors.find((item) => item.code.some((r) => errorWords.indexOf(r) !== -1));

    if (action) {
        return action.action();
    } else {
        message.error(error.message);
    }
};

const goMusic = (to, from, next) => {
    next({ name: 'music' });
};

const routes = [
    {
        path: '/empty',
        name: 'empty',
        component: () => import('../pages/Empty.vue'),
        abort: []
    },
    {
        path: '/music',
        name: 'music',
        component: () => import('../pages/Music/Music.vue'),
        abort: []
    },
        {
            path: '/categories/:code',
            name: 'category',
            component: () => import('../pages/Dictionaries/Category.vue'),
            props: true,
            abort: []
        },
        {
            path: '/moods/:code',
            name: 'mood',
            component: () => import('../pages/Dictionaries/Mood.vue'),
            props: true,
            abort: []
        },
        {
            path: '/genres/:code',
            name: 'genre',
            component: () => import('../pages/Dictionaries/Genre.vue'),
            props: true,
            abort: []
        },
    {
        path: '/favorite',
        name: 'favorite',
        component: () => import('../pages/Favorite/Favorite.vue'),
        abort: []
    },
        {
            path: '/favorite/artists',
            name: 'favorite-artists',
            component: () => import('../pages/Favorite/Artists/Artists.vue'),
            abort: []
        },
        {
            path: '/favorite/playlists',
            name: 'favorite-playlists',
            component: () => import('../pages/Favorite/Playlists/Playlists.vue'),
            abort: []
        },
        {
            path: '/favorite/my-artists',
            name: 'favorite-my-artists',
            component: () => import('../pages/Favorite/MyArtists/MyArtists.vue'),
            abort: []
        },
    {
        path: '/settings',
        name: 'settings',
        component: () => import('../pages/Settings/Settings.vue'),
        abort: []
    },
    {
        path: '/error',
        name: 'error',
        component: () => import('../pages/Error.vue'),
        abort: []
    },
    {
        path: '/access-denied',
        name: 'access-denied',
        component: () => import('../pages/AccessDenied.vue'),
        abort: []
    },
    {
        path: '/:pathMatch(.*)*',
        name: 'error-page',
        beforeEnter: goMusic
    }
];

const router = createRouter({
    history: createWebHistory(import.meta.env.VITE_APP_BASE_URL),
    routes
});

router.back = function (path) {
    this.options?.history?.state?.back ? this.go(-1) : this.push(path || '/');
};

router.beforeEach((to, from, next) => {
    if (stores.modals().back()) {
        next(false);
        return;
    }

    if (from?.name) {
        routes.forEach((item) => {
            if (item?.name === from?.name && item.abort?.length) {
                item.abort.forEach((code) => {
                    if (code.split(':id').length === 1) {
                        requests[code].abort();
                    } else {
                        requests[code.split(':id')[0]].abort(from.params.id);
                    }
                });
            }
        });
    }

    if (to?.name && stores.nav().page !== to.name) {
        stores.nav().setPage(to.name);
    }
    next();
});

router.afterEach((to, from) => {
    if (to.name === from.name) {
        //  , ,   timestamp
        componentKey.value = `${to.fullPath || to.name || 'default'}-${Date.now()}`;
    } else {
        //         ( )
        componentKey.value = to.fullPath || to.name || 'default';
    }
});

window.q = componentKey;

router.onError(handleRouterError);

export { componentKey };
export default router;
