import { defineStore } from 'pinia';
import stores from '/stores';
import { baseUrl } from '/utils/utils';

const useOthersStore = defineStore('others', {
    state: () => ({
        ready: false,
        pageReady: null
    }),
    getters: {
        theme: (state) => {
            return stores.settings().theme || stores.tg().theme;
        },
        themePath: (state) => {
            if (state.theme === 'dark') {
                return new URL(`/themes/dark/variables.css?v=2`, import.meta.url).href;
            }
            return new URL(`/themes/light/variables.css?v=2`, import.meta.url).href;
        },
        svgSpritePath: (state) => {
            if (state.theme === 'dark') {
                return new URL(`/themes/dark/svg-sprite.svg`, import.meta.url).href;
            }
            return new URL(`/themes/light/svg-sprite.svg`, import.meta.url).href;
        },
        animateSvgSpritePath: (state) => {
            if (state.theme === 'dark') {
                return `${baseUrl}/themes/dark/animate`;
            }
            return `${baseUrl}/themes/light/animate`;
        }
    },
    actions: {
        setReady(payload) {
            this.ready = payload;
        },
        setPageReady(payload) {
            this.pageReady = payload;
        }
    }
});

export default useOthersStore;
